body, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, th, td, form, fieldset, legend, input, textarea, img, button, select {
  margin: 0;
  padding: 0;
}

body {
  -webkit-text-size-adjust: none;
}

img, fieldset {
  border: 0;
  vertical-align: top;
}

a {
  color: #1a1a1a;
}

em, address {
  font-style: normal;
}

ul, ol {
  list-style: none;
}

label, button {
  cursor: pointer;
}

.sp_sona {
  display: inline-block;
  vertical-align: top;
  background: url("/src/sona/imgs/sp_sona.png") 0 0 no-repeat;
  background-size: 500px 1000px;
  line-height: 100em;
  overflow: hidden;
}

.emphasis_text {
  color: #1c90fb;
}

.error_text {
  color: #fc5356;
}

.SN_groupset {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.rotate180 {
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

.blind {
  position: absolute !important;
  clip: rect(0 0 0 0) !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
}

.SN_btn {
  position: relative;
  display: inline-block;
  vertical-align: top;
  background: transparent;
  border: 0;
}

.SN_btn .btn_skin {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 30px;
  padding: 0 6px;
  border-radius: 4px;
  font-size: 14px;
  text-decoration: none;
  vertical-align: middle;
  box-sizing: border-box;
  cursor: pointer;
  height: 30px;
  border: 1px solid #b4b4b4;
  background: #fff;
  color: #4a4a4a;
}

.SN_btn .btn_skin:hover {
  border-color: #4a4a4a;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.SN_btn .btn_skin .label {
  margin: 0 4px;
  padding-top: 1px;
  line-height: 1;
}

.SN_btn .btn_skin .raster_img {
  display: inline-block;
  vertical-align: top;
}

.SN_btn .btn_skin .svg_img {
  width: 1em;
  height: 1em;
  fill: #b4b4b4;
}

.SN_btn.c_blue .btn_skin {
  border: 1px solid #1c90fb;
  background: #1c90fb;
  color: #fff;
}

.SN_btn.c_blue .btn_skin:hover {
  border-color: #0778e6;
  background-color: #0778e6;
  box-shadow: 0 3px 4px 0 rgba(7, 120, 230, 0.2);
}

.SN_btn.c_black .btn_skin {
  border: 1px solid #273047;
  background: #273047;
  color: #fff;
}

.SN_btn.c_black .btn_skin:hover {
  border-color: #10172b;
  background-color: #10172b;
  box-shadow: 0 3px 4px 0 rgba(16, 23, 43, 0.4);
}

.SN_btn.c_navy .btn_skin {
  border: 1px solid #495d88;
  background: #495d88;
  color: #fff;
}

.SN_btn.c_navy .btn_skin:hover {
  border-color: #2d3d5f;
  background-color: #2d3d5f;
}

.SN_btn.c_blueline .btn_skin {
  border: 1px solid #1c90fb;
  background: #fff;
  color: #1c90fb;
}

.SN_btn.c_blueline .btn_skin:hover {
  background-color: #e2f1fe;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.11);
}

.SN_btn.selected .btn_skin {
