  color: #468ff3;
}

.SN_btn.selected .btn_skin .svg_img {
  fill: #1c90fb;
}

.SN_btn.s_large .btn_skin {
  height: 50px;
  min-width: 50px;
  padding: 0 30px;
}

.SN_btn.s_large.d_round .btn_skin {
  padding: 0 30px;
}

.SN_btn.s_primary .btn_skin {
  height: 38px;
  min-width: 38px;
  padding: 0 20px;
}

.SN_btn.s_primary.d_round .btn_skin {
  padding: 0 20px;
}

.SN_btn.s_medium .btn_skin {
  height: 26px;
  min-width: 26px;
}

.SN_btn.s_small .btn_skin {
  height: 23px;
  min-width: 23px;
  font-size: 12px;
}

.SN_btn.d_round .btn_skin {
  padding: 0 10px;
  border-radius: 100px;
}

.SN_btn.d_textbtn .btn_skin {
  border: 1px solid transparent;
  background: transparent;
  color: #777;
}

.SN_btn.d_textbtn .btn_skin:hover {
  background-color: #fafafa;
  box-shadow: none;
  color: #4a4a4a;
}

.SN_btn.d_textbtn.s_small .btn_skin {
  height: 16px;
}

.SN_btn.d_textbtn.s_small .btn_skin:hover {
  background-color: transparent;
