}

.SN_btn .SN_spinner.arr_center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.SN_btn[disabled] .btn_skin, .SN_btn[disabled]:hover .btn_skin {
  border: 1px solid #d3d3d3;
  background: #f4f4f4;
  color: #b4b4b4;
  box-shadow: none;
  cursor: not-allowed;
}

.SN_btn.file_upload.disabled .btn_skin {
  border: 1px solid #d3d3d3;
  background: #f4f4f4;
  color: #b4b4b4;
  box-shadow: none;
  cursor: not-allowed;
}

.SN_btn.file_upload.c_blue.disabled input[disabled]:hover .btn_skin, .SN_btn.file_upload.c_black.disabled input[disabled]:hover .btn_skin, .SN_btn.file_upload.c_navy.disabled input[disabled]:hover .btn_skin {
  border: 1px solid #d3d3d3;
  background: #d3d3d3;
  color: #f4f4f4;
}

.SN_btn.file_upload.c_blue.disabled .btn_skin, .SN_btn.file_upload.c_black.disabled .btn_skin, .SN_btn.file_upload.c_navy.disabled .btn_skin {
  border: 1px solid #d3d3d3;
  background: #d3d3d3;
  color: #f4f4f4;
  font-weight: 700;
  box-shadow: none;
  cursor: not-allowed;
}

.SN_btn.file_upload.orign .btn_skin {
  padding: 0 4px;
}

.SN_btn.file_upload.orign.s_large .btn_skin {
  padding: 0 14px;
}

.SN_btn.file_upload.orign.s_primary .btn_skin {
  padding: 0 8px;
}

.SN_btn.file_upload.orign.s_medium .btn_skin {
  padding: 0 1px;
}

.SN_btn.file_upload.orign.s_small .btn_skin {
  padding: 0;
}

.SN_btn.file_upload .input_file {
  width: 100%;
}

.SN_btn.c_blue.disabled[disabled]:hover .btn_skin, .SN_btn.c_black.disabled[disabled]:hover .btn_skin, .SN_btn.c_navy.disabled[disabled]:hover .btn_skin {
  border: 1px solid #d3d3d3;
  background: #d3d3d3;
  color: #f4f4f4;
}

.SN_btn.c_blue.disabled .btn_skin, .SN_btn.c_black.disabled .btn_skin, .SN_btn.c_navy.disabled .btn_skin {
  border: 1px solid #d3d3d3;
  background: #d3d3d3;
  color: #f4f4f4;
  font-weight: 700;
  box-shadow: none;
  cursor: not-allowed;
}

.SN_btngroup {
  display: inline-block;
  vertical-align: top;
}

.SN_btngroup .group_box {
  display: flex;
  align-items: center;
}

.SN_btngroup .SN_btn + .SN_btn {
  margin-left: 4px;
}

.SN_togglebtn {
  display: inline-flex;
}

.SN_togglebtn .SN_btn + .SN_btn {
  margin-left: -1px;
}

.SN_togglebtn .SN_btn:hover {
  z-index: 2;
}

.SN_togglebtn .SN_btn.selected {
  z-index: 1;
}

.SN_togglebtn .SN_btn .btn_skin {
  border-radius: 0;
}

.SN_togglebtn .SN_btn:first-child .btn_skin {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.SN_togglebtn .SN_btn:last-child .btn_skin {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.SN_togglebtn .SN_btn.s_large .btn_skin {
  padding: 0 8px;
}

.SN_togglebtn .SN_btn.s_large .svg_img {
  width: 1.5em;
  height: 1.5em;
}

.SN_togglebtn .SN_btn.s_primary .btn_skin {
  padding: 0 6px;
}

.SN_togglebtn .SN_btn.s_primary .svg_img {
  width: 1.25em;
  height: 1.25em;
}

.SN_togglebtn .SN_btn.s_medium .btn_skin {
  padding: 0 4px;
}

.SN_togglebtn .SN_btn.s_small .btn_skin {
  padding: 0 3px;
}

.SN_togglebtn .SN_btn[disabled],
.SN_togglebtn .SN_btn[disabled]:hover .SN_togglebtn .SN_btn.disabled {
  z-index: auto;
}

.SN_togglebtn.twins .SN_btn + .SN_btn {
  margin-left: -3px;
}

.SN_togglebtn.twins .SN_btn .btn_skin {
  border-radius: 4px;
}

.SN_togglebtn.portrait {
  flex-direction: column;
}

.SN_togglebtn.portrait .SN_btn + .SN_btn {
  margin-top: -1px;
  margin-left: 0;
}

.SN_togglebtn.portrait .SN_btn .btn_skin {
  width: 100%;
  border-radius: 0;
}

.SN_togglebtn.portrait .SN_btn:first-child .btn_skin {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-left-radius: 0;
}

.SN_togglebtn.portrait .SN_btn:last-child .btn_skin {
  border-top-right-radius: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.SN_formbtn {
  position: relative;
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
}

.SN_formbtn.d_single {
  vertical-align: middle;
  width: 14px;
  height: 14px;
}

.SN_formbtn + .SN_formbtn {
  margin-left: 4px;
}

.SN_formbtn.vertical {
  display: block;
}

.SN_formbtn.vertical + .SN_formbtn {
  margin-left: 0;
}

.SN_formbtn .SN_input {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.SN_formbtn .SN_input + .icon {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -7px;
  width: 14px;
  height: 14px;
  background: url("/src/sona/imgs/sp_sona.png") 0 0 no-repeat;
  background-size: 500px 1000px;
  line-height: 100em;
  overflow: hidden;
}

.SN_formbtn .SN_input[type=checkbox] + .icon {
  background-position: -328px -20px;
}

.SN_formbtn .SN_input[type=checkbox]:checked + .icon {
  background-position: -347px -20px;
}

.SN_formbtn .SN_input[type=checkbox]:checked:disabled + .icon {
  background-position: -385px -20px;
}

.SN_formbtn .SN_input[type=checkbox]:disabled + .icon {
  background-position: -366px -20px;
  cursor: not-allowed;
}

.SN_formbtn .SN_input[type=checkbox]:disabled ~ .label_text {
  cursor: not-allowed;
}

.SN_formbtn .SN_input[type=checkbox].focus + .icon {
  background-position: -362px -63px;
}

.SN_formbtn .SN_input[type=checkbox].focus:checked + .icon {
  background-position: -379px -63px;
}

.SN_formbtn .SN_input[type=checkbox].half_check:checked + .icon {
  background-position: -413px -63px;
}

.SN_formbtn .SN_input[type=checkbox].half_check:checked:disabled + .icon {
  background-position: -430px -63px;
}

.SN_formbtn .SN_input[type=checkbox].half_check.focus:checked + .icon {
  background-position: -396px -63px;
}

.SN_formbtn .SN_input[type=radio] + .icon {
  background-position: -404px -20px;
}

.SN_formbtn .SN_input[type=radio]:checked + .icon {
  background-position: -423px -20px;
}

.SN_formbtn .SN_input[type=radio]:checked:disabled + .icon {
  background-position: -461px -20px;
}

.SN_formbtn .SN_input[type=radio]:disabled + .icon {
  background-position: -442px -20px;
  cursor: not-allowed;
}

.SN_formbtn .SN_input[type=radio]:disabled ~ .label_text {
  cursor: not-allowed;
}

.SN_formbtn .SN_input[type=radio].focus + .icon {
  background-position: -447px -63px;
}

.SN_formbtn .SN_input[type=radio].focus:checked + .icon {
  background-position: -464px -63px;
}

.SN_formbtn .label_text {
  display: inline-block;
  padding-left: 18px;
  box-sizing: border-box;
  font-size: 13px;
  line-height: 16px;
  color: #4a4a4a;
}

.SN_formbtn_round {
  position: relative;
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
}

.SN_formbtn_round + .SN_formbtn_round {
  margin-left: 4px;
}

.SN_formbtn_round .SN_input {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.SN_formbtn_round .SN_input + .icon {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -8px;
  width: 16px;
  height: 16px;
  background: url("/src/sona/imgs/sp_sona.png") 0 0 no-repeat;
  background-size: 500px 1000px;
  line-height: 100em;
  overflow: hidden;
}

.SN_formbtn_round .SN_input[type=checkbox] + .icon {
  background-position: -328px -40px;
}

.SN_formbtn_round .SN_input[type=checkbox]:checked + .icon {
  background-position: -349px -40px;
}

.SN_formbtn_round .SN_input[type=checkbox]:checked:disabled + .icon {
  background-position: -370px -40px;
}

.SN_formbtn_round .label_text {
  display: inline-block;
  padding-left: 20px;
  box-sizing: border-box;
  font-size: 13px;
  line-height: 16px;
  color: #4a4a4a;
}

.SN_switch {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.SN_switch > label {
  position: relative;
  display: block;
}

.SN_switch .switch_input {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.SN_switch .switch_input:checked ~ .switch_img {
  left: auto;
  right: 2px;
}

.SN_switch .switch_input:checked ~ .label_text {
  padding-left: 5px;
  padding-right: 20px;
  background: #1c90fb;
  color: #fff;
}

.SN_switch .switch_input:checked ~ .label_text.off {
  display: none;
}

.SN_switch .switch_input:checked ~ .label_text.on {
  display: block;
}

.SN_switch .switch_input:disabled ~ .switch_img {
  background: #f8f8f8;
  box-shadow: none;
}

.SN_switch .switch_input:disabled ~ .label_text {
  background: #efefef;
  color: #d3d3d3;
}

.SN_switch .switch_img {
  position: absolute;
  top: 50%;
  left: 2px;
  margin-top: -8px;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 50%;
}

.SN_switch .label_text {
  display: none;
  padding-left: 21px;
  box-sizing: border-box;
  width: 40px;
  height: 20px;
  background: #e2e2e2;
  border-radius: 50px;
  text-align: left;
  font-size: 10px;
  font-weight: 700;
  line-height: 21px;
  color: #aaa;
  overflow: hidden;
}

.SN_switch .label_text.off {
  display: block;
}

.SN_switch.s_large .switch_input:checked ~ .switch_img {
  left: auto;
  right: 4px;
}

.SN_switch.s_large .switch_input:checked ~ .label_text {
  padding-left: 10px;
  padding-right: 32px;
}

.SN_switch.s_large .label_text {
  padding-left: 32px;
  width: 60px;
  height: 30px;
  font-size: 13px;
  line-height: 31px;
}

.SN_switch.s_large .switch_img {
  left: 4px;
  margin-top: -12px;
  width: 24px;
  height: 24px;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.15);
}

.SN_switch.disabled .switch_img {
  background: #f8f8f8;
  box-shadow: none;
}

.SN_switch.disabled label {
  cursor: not-allowed;
}

/*----- Input -----*/
.SN_input {
  position: relative;
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  height: 30px;
  border: 1px solid #d3d3d3;
  background: #fff;
  border-radius: 4px;
}

.SN_input .textinput {
  padding: 0 8px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 0;
  background: 0;
  font-size: 14px;
  letter-spacing: -.7px;
  color: #4a4a4a;
  outline: 0;
}

.SN_input:hover, .SN_input.on {
