}

.SN_input.error .validation_text {
  color: #fc5356;
}

.SN_input.warning {
  border-color: #ffb100;
}

.SN_input.warning .validation_text {
  color: #ffb100;
}

.SN_input.d_password {
  padding-right: 30px;
}

.SN_input.d_password:before {
  content: '';
  position: absolute;
  top: 50%;
  right: 6px;
  margin-top: -7px;
  width: 12px;
  height: 14px;
  background: url("/src/sona/imgs/sp_sona.png") -251px 0;
