/*----- Tooltip -----*/
.SN_tooltip {
  position: relative;
  padding: 8px 10px;
  box-sizing: border-box;
  max-width: 500px;
  background: rgba(20, 20, 20, 0.85);
  border-radius: 5px;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  font-size: 11px;
  color: #fff;
  z-index: 10;
  word-break: break-all;
  overflow-wrap: break-word;
}

.SN_tooltip:before {
  content: '';
  position: absolute;
  border-top: 8px solid rgba(20, 20, 20, 0.85);
  border-left: 8px solid transparent;
}

.SN_tooltip.top {
  /*bottom:100%;
    left:50%;
    margin-bottom:7px;
    -webkit-transform:translate(-50%,0);
    -ms-transform:translate(-50%,0);
    -o-transform:translate(-50%,0);
    transform:translate(-50%,0);*/
}

.SN_tooltip.top:before {
  top: 100%;
  left: 50%;
  margin-top: -4px;
  margin-left: -4px;
  -webkit-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

.SN_tooltip.top.left:before {
  left: 10px;
  margin-left: 0;
}

.SN_tooltip.top.right:before {
  left: auto;
  right: 10px;
  margin-left: 0;
}

.SN_tooltip.mid {
  /*top:50%;
    -webkit-transform:translate(0,-50%);
    -ms-transform:translate(0,-50%);
    -o-transform:translate(0,-50%);
    transform:translate(0,-50%);*/
}

.SN_tooltip.mid:before {
  top: 50%;
  margin-top: -4px;
}

.SN_tooltip.mid.left {
  /*right:100%;
      margin-right:7px;*/
}

.SN_tooltip.mid.left:before {
  left: 100%;
  margin-left: -4px;
  transform: rotate(45deg);
}

.SN_tooltip.mid.right {
  /*left:100%;
      margin-left:7px;*/
}

.SN_tooltip.mid.right:before {
  left: auto;
  right: 100%;
  margin-right: -4px;
  transform: rotate(-135deg);
}

.SN_tooltip.bottom {
  /*top:100%;
    left:50%;
    margin-top:7px;
    -webkit-transform:translate(-50%,0);
    -ms-transform:translate(-50%,0);
    -o-transform:translate(-50%,0);
    transform:translate(-50%,0);*/
}

.SN_tooltip.bottom:before {
  bottom: 100%;
  left: 50%;
  margin-bottom: -4px;
  margin-left: -4px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.SN_tooltip.bottom.left:before {
  left: 10px;
  margin-left: 0;
}

.SN_tooltip.bottom.right:before {
  left: auto;
  right: 10px;
  margin-left: 0;
}

.SN_tooltip.d_white {
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0 2px 10px 2px rgba(0, 0, 0, 0.16);
  color: #4a4a4a;
}

.SN_tooltip.d_white:before {
  border-color: rgba(255, 255, 255, 0.9);
}

.SN_tooltip.d_white.left {
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.16);
}

.SN_tooltip.d_white.right {
  box-shadow: -2px 2px 10px 2px rgba(0, 0, 0, 0.16);
}

.SN_tooltip.d_white.bottom {
  box-shadow: 0 -1px 10px 2px rgba(0, 0, 0, 0.16);
}

