/*----- Toast -----*/
.SN_toast {
  display: none;
  position: fixed;
  bottom: 30px;
  left: 50%;
  background: #1fc47c;
  border-radius: 6px;
  padding: 11px 24px 11px 45px;
  transform: translateX(-50%);
}

.SN_toast:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 6px;
  width: 37px;
  height: 37px;
  margin-top: -17px;
  background: url("/src/sona/imgs/sp_sona.png") 0 0 no-repeat;
  background-size: 500px 1000px;
}

.SN_toast.i_confirm {
  background: #1fc47c;
}

.SN_toast.i_confirm:before {
  background-position: -97px -221px;
}

.SN_toast.i_refuse {
  background: #fc5356;
}

.SN_toast.i_refuse:before {
  background-position: -139px -221px;
}

.SN_toast.i_info {
  background: #1c90fb;
}

.SN_toast.i_info:before {
  background-position: -55px -221px;
}

.SN_toast .msg {
  display: block;
  font-size: 16px;
  color: #fff;
  letter-spacing: -.8px;
  line-height: 25px;
}

.SN_toast.run_ani {
  display: inline-block;
  animation: toast_fadein 0.5s, toast_fadeout 0.5s 2.5s;
}

@keyframes toast_fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes toast_fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

