/*----- spinner -----*/
.SN_spinner {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.SN_spinner .loading {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  border-radius: 50%;
  border-top: 5px solid rgba(0, 188, 212, 0.2);
  border-right: 5px solid rgba(0, 188, 212, 0.2);
  border-bottom: 5px solid rgba(0, 188, 212, 0.2);
  border-left: 5px solid #00bcd4;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: loading_spin 1s infinite linear;
  -ms-animation: loading_spin 1s infinite linear;
  -o-animation: loading_spin 1s infinite linear;
  animation: loading_spin 1s infinite linear;
}

.SN_spinner.c_blue .loading {
  border-top-color: rgba(28, 144, 251, 0.2);
  border-right-color: rgba(28, 144, 251, 0.2);
  border-bottom-color: rgba(28, 144, 251, 0.2);
  border-left-color: #1c90fb;
}

.SN_spinner.s_small .loading {
  width: 15px;
  height: 15px;
  border-width: 3px;
}

@keyframes loading_spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes loading_spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
